:host {
  @include reset-host;
}

:host .click-action {
  height: 100%;
}

:host .article-card {
  display: flex;
  gap: rem(16);
  align-items: center;
  width: 100%;
  height: 100%;
  padding: rem(24);
  background-color: $gray-200;

  @include mq('min', 'md') {
    flex-direction: column;
    align-items: flex-start;
    width: rem(240);
    padding: rem(40) rem(24) rem(24);
  }
}

:host .article-title {
  flex: 1;
  font-size: rem(16);

  @include mq('min', 'md') {
    margin-bottom: rem(20);
  }
}

:host .article-button {
  display: none;
  font-size: rem(14);
  color: $white;

  &:hover {
    color: $white;
  }

  @include mq('min', 'md') {
    display: block;
  }
}

:host .article-image {
  position: relative;
  width: rem(80);
  height: rem(80);

  @include mq('min', 'md') {
    width: 100%;
    height: rem(128);
  }
}
