import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import type { BsImage } from '../../atoms/image/image';
import { TitleColor, TitleSize, TitleTag } from '../../atoms/title/title.types';
import { ButtonVariant } from '../../atoms/button/button.types';
import { IconName } from '../../atoms/icon/icon.types';

/**
 * Article teaser card component.
 */
@Component({
  tag: 'bs-article-card',
  styleUrl: 'article-card.scss',
  shadow: true,
})
export class BsArticleCard {
  /**
   * The article related image.
   */
  @Prop() articleImage: BsImage | string;

  @Memoize('articleImage')
  get parsedArticleImage(): BsImage {
    return parseIfString(this.articleImage);
  }

  /**
   * The article title.
   */
  @Prop() articleTitle: string;

  /**
   * The cta label.
   */
  @Prop() articleCta: string;

  /**
   * The article href.
   */
  @Prop() articleHref: string;

  render() {
    return (
      <Host>
        <bs-click-action class="click-action" href={this.articleHref}>
          <div class="article-card">
            <bs-title
              class="article-title"
              tag={TitleTag.Span}
              size={TitleSize.Head5}
              text={this.articleTitle}
              color={TitleColor.White}
            />
            <bs-button
              class="article-button"
              text={this.articleCta}
              iconRight={IconName.CaretForward}
              href={this.articleHref}
              variant={ButtonVariant.Text}
            />
            <bs-image
              class="article-image"
              cover={true}
              src={this.parsedArticleImage.src}
              srcset={this.parsedArticleImage.srcset}
              imageSizes="200px, 400px"
              altText={this.parsedArticleImage.altText}
            />
          </div>
        </bs-click-action>
      </Host>
    );
  }
}
